.hhv-phuong-an {
	.content.tabs {
		padding: 0;
		.btabs {
			.tab-title {
				li {
					margin-right: 100px;
					padding-bottom: 40px;
					padding-left: 5px;
					position: relative;
					
					a {
						padding: 0;
						border: 0;
					}
					.radio-custom1 {
						padding-left: 28px;
						.checkmark {
							top: -1px;
						}
					}
					&.active {
						&:before {
							position: absolute;
							content: "";
							bottom: 0;
							left: 0;
						    border-width: 14px;
						    border-top-color: transparent;
						    border-left-color: transparent;
						    border-right-color: transparent;
						    border-bottom-color: #f7f7f7;
						    border-style: solid;
						}
					}
					&.active, &:hover {
						a {
						    color: #576C8A;
						    font-weight: normal;
						}
						.radio-custom1 {
   							.checkmark {
   								border-color: #EE0033;
   								&:after {
									display: block;
									background: #EE0033;
									width: 10px;
									height: 10px;
									border-radius: 100%;
									position: absolute;
									top: 3px;
									left: 3px;
   								}
   							}
						}
					}
				}
			}
		}
		.tabs-content {
			position: relative;
			background-color: #f7f7f7;
			&:before {
				left: 100%;
				top: 0;
				bottom: 0;
				width: 1000px;
				background-color: #f7f7f7;
				content: "";
				position: absolute;
			}
			&:after {
				right: 100%;
				top: 0;
				bottom: 0;
				width: 1000px;
				background-color: #f7f7f7;
				content: "";
				position: absolute; 
			}
			.sub-tabs {
				.tabcontent {
					padding: 0;
					.inner-tabs {
						padding: 40px 0;
						.inner-item {
						    .radio-cs {
						    	input {
									position: absolute;
									opacity: 0;
									cursor: pointer;
									height: 0;
									width: 0;
						    	}
						    	.checkmark1 {
									box-shadow: 0px 8px 15px rgba(39, 52, 125, 0.12);
								    border-radius: 10px;
								    text-align: center;
								    padding: 60px 15px;
								    background-color: #fff;
								    color: #EE0033;
								    font-size: 20px;
							    	h4 {
							    		color: #0C0033;
							    		font-weight: bold;
										font-size: 24px;
										margin-bottom: 15px;
							    	}
							    	&:hover {
							    		background: rgba(20, 175, 180, 0.15);
						    			box-shadow: none;
						    			cursor: pointer;
							    	}
						    	}
						    	input:checked ~ .checkmark1 {
						    		background: rgba(20, 175, 180, 0.15);
						    		box-shadow: none;
						    	}
						    }
						}
					}
				}
			}
		}
	}
}

