div.search.top-search {
	z-index: 111;
	div.form-search {
		position: relative;
		z-index: 100;
		.history-search {
			display: none;
			position: absolute;
			top: 100%;
			right: 0;
			z-index: 1;
			padding-top: 20px;
			.inner-search {
				display: inline-block;
				vertical-align: top;
			    box-shadow: 0px 14px 40px rgba(0, 0, 0, 0.3);
				background-color: #fff;
				border-radius: 10px;
				min-width: 320px;
				position: relative;
				&:before {
					content: "";
					position: absolute;
					border-width: 20px;
					border-style: solid;
					border-left-color: transparent;
					border-right-color: transparent;
					border-top-color: transparent;
					border-bottom-color: #fff;
				    bottom: 100%;
				    right: 30px;
				}
				.box-search {
					padding: 20px 20px;
					border-bottom: 1px solid #E7E7E7;
					&:last-child {
						border-bottom: 0;
					}
					h4.title {
						color: #BFC4CE;
						font-size: 14px;
						margin-bottom: 10px;
					}
					.tag-search {
						a {
							display: inline-block;
							vertical-align: top;
							padding: 5px 18px;
							border: 1px solid #EE0033;
							border-radius: 8px;
							line-height: 17px;
							color: #EE0033;
							margin: 4px 2px;
							&:hover {
								color: #FFF;
								background-color: #EE0033;

							}
						}
					}
					.list-search {
						li {
							margin: 7px 0;
							line-height: 20px;
							&:hover {
								a {
									color: #EE0033;
								}
							}
						}
					}
					.delete-history {
						a.btn {
							width: 100%;
							border: 1px solid #FF0000;
							color: #FF0000;
							&:hover {
								background-color: #FF0000;
								color: #fff;
							}
						}
					}
				}
			}
		}
		&:hover {
			.history-search {
				display: block;
			}
		}
	}
}

