						font-weight: bold;
						font-size: 16px;
						line-height: 24px;
					}
					&:after {
						display: none;
					}
					&.checked {
						.bar__progress__text__normal,
						.bar__progress__text__bold {
							color: #B70D02;
						}
					}
					&.check {
