.clearfix {
	clear: both;

	&:after {
		display: block;
		content: "";
		clear: both;
	}
}

.main-pay {
	margin-bottom: 74px;

	.pathway {
		margin-top: 41px;
	}
}

.row-menu.menu-pay .boxmenu ul li a {
	padding: 15px 20px;
}

.h1-title-pay {
	font-family: Montserrat;
	font-style: normal;
	font-weight: bold;
	font-size: 24px;
	color: #0C0033;
	line-height: 36px;
	text-align: center;
	margin-top: 65px;
}

.main-pay .content.tabs {
	.tabs-inner {
		.nav-tabs {
			display: flex;
			justify-content: space-between;
			padding: 22px 0 65px;

			li {
				background: #FFFFFF;
				box-shadow: 0px 10px 40px rgba(39, 52, 125, 0.12);
				border-radius: 10px;
				width: 18%;
				height: 156px;
				text-align: center;
				position: relative;

				.icon-tab {
					width: 100%;
					display: block;
					margin-bottom: 16px;
					margin-top: 32px;

					&.icon-tc {
						background: url('../images/icon-tc.png') no-repeat center;
						height: 48px;
						margin-top: 34px;
					}

					&.icon-tk {
						background: url('../images/icon-tk.png') no-repeat center;
						height: 54px;
					}

					&.icon-ut {
						background: url('../images/icon-ut.png') no-repeat center;
						height: 56px;
						margin-top: 30px;
					}

					&.icon-tctt {
						background: url('../images/icon-tctt.png') no-repeat center;
						height: 56px;
						margin-top: 30px;
					}

					&.icon-xlnt {
						background: url('../images/icon-xlnt.png') no-repeat center;
						height: 56px;
					}
				}

				&.active {
					a {
						color: #EE0033;
					}

					.icon-tc {
						background-image: url('../images/icon-tc-active.png');
					}

					.icon-tk {
						background-image: url('../images/icon-tk-active.png');
					}

					.icon-ut {
						background-image: url('../images/icon-ut-active.png');
					}

					.icon-tctt {
						background-image: url('../images/icon-tctt-active.png');
					}

					.icon-xlnt {
						background-image: url('../images/icon-xlnt-active.png');
					}

				}

				a {
					font-family: Roboto;
					font-style: normal;
					font-weight: 500;
					font-size: 18px;
					line-height: 24px;
					letter-spacing: -0.5px;
					color: #576C8A;
					width: 140px;
					display: inline-block;
					word-break: break-all;
				}

				span.sale-off {
					font-family: Montserrat;
					font-style: normal;
					font-weight: 500;
					font-size: 14px;
					line-height: 40px;
					text-align: center;
					color: #FFFFFF;
					width: 40px;
					height: 40px;
					background: #FF0048;
					border-radius: 50%;
					position: absolute;
					top: 18px;
					right: 48px;
				}
			}
		}
	}

	.tabcontent {
		padding: 0 141px 0 98px;

		.col-payl {
			width: 62%;

			.row-group {
				margin: 0 0 16px;

				label {
					font-family: Roboto;
					font-style: normal;
					font-weight: 500;
					font-size: 16px;
					line-height: 19px;
					color: #0C0033;
				}

				.control {
					.form-control {
						background-position-x: calc(96%);
						background-position-y: 50%;
					}

					input,
					select {
						font-size: 14px;
					}

					.box-capcha {
						border: 1px solid #C4C4C4;
						border-radius: 10px;
						width: 193px;
						position: relative;
						padding-right: 36px;
						height: 58px;
						margin-top: 8px;

						.img {
							display: inline-block;

							img {
								height: 37px;
								max-height: none;
								margin: 10px 5px;
							}
						}

						.refesh {
							position: absolute;
							right: 9px;
							top: 16px;
							width: 25px;
							height: 25px;
							display: block;
							cursor: pointer;
						}
					}

					.currency {
						margin-top: 8px;

						ul {
							li {
								float: left;
								width: 109px;
								height: 44px;
								background: #B70D02;
								border-radius: 10px;
								margin-right: 16px;
								margin-bottom: 16px;
								text-align: center;
								transition: all 0.3s;

								a {
									font-family: Montserrat;
									font-style: normal;
									font-weight: normal;
									font-size: 14px;
									line-height: 44px;
									display: block;
									align-items: center;
									text-align: center;
									color: #FFFFFF;
								}

								&:nth-child(3n+3) {
									margin: 0;
								}

								&:hover {
									background: #EE0033;
								}
							}
						}

						span {
							font-family: SF Pro Text;
							font-size: 12px;
							line-height: 18px;
							display: block;
							clear: both;
							color: #FE9A00;
						}
					}
				}
			}

			.note-pay {
				font-family: Roboto;
				font-style: italic;
				font-weight: 300;
				font-size: 14px;
				line-height: 24px;
				color: #576C8A;
				margin-top: 25px;
				padding-left: 195px;

				span {
					color: #FF0048;
				}
			}

			.action {
				padding-left: 195px;
				margin-top: 23px;

				.action-pay {
					padding: 15px 50px;
					margin-right: 23px;
				}

				.action-back {
					font-family: Roboto;
					font-style: normal;
					font-weight: normal;
					font-size: 18px;
					line-height: 21px;
					text-decoration-line: underline;
					color: #EE0033;
					transition: all 0.3s;

					&:hover {
						color: #B70D02;
					}
				}
			}
		}

		.col-pay2 {
			.support {
				ul {
					li {
						margin-bottom: 34px;

						a {
							font-family: Roboto;
							font-style: normal;
							font-weight: normal;
							font-size: 16px;
							line-height: 180%;
							color: #0C0033;
							position: relative;

							&:before {
								content: '';
								height: 24px;
								width: 24px;
								display: block;
								float: left;
								margin-right: 16px;
							}
						}

						&.support-cskh {
							a {
								&:before {
									background: url('../images/icon-cskh.png') no-repeat;
								}
							}
						}

						&.support-mb {
							a {
								&:before {
									background: url('../images/icon-phone.png') no-repeat;
								}
							}
						}

						&.support-mn {
							a {
								&:before {
									background: url('../images/icon-phone.png') no-repeat;
								}
							}
						}

						&.support-mail {
							a {
								&:before {
									background: url('../images/icon-mail.png') no-repeat;
								}
							}
						}

						&.support-mess {
							a {
								&:before {
									background: url('../images/icon-mess.png') no-repeat;
								}
							}
						}
					}
				}
			}
		}
	}
}

