/* TIN TUC SU KIEN */
.page-ttsk {
	.pathway {
		margin: 40px 0 40px;
	}

	.content.tabs {
		.tit {
			margin: 0px 0px 68px;
		}

		.tabcontent {
			.content-tab {
				display: flex;
				flex-wrap: wrap;

				.item {
					max-width: 360px;
					float: left;
					margin-right: 30px;
					margin-bottom: 30px;

					&.first-item {
						max-width: 750px;
						position: relative;

						.item-image {
							&:before {
								border-radius: 10px;
							}
						}

						.item-content {
							position: absolute;
							left: 0;
							bottom: 0;
							width: 48%;
							border-radius: 0px 0px 0px 10px;
						}
					}

					&:nth-child(2),
					&:nth-child(5) {
						margin-right: 0;
					}

					&:hover {
						.item-image {
							&:before {
								opacity: 1;
							}
						}
					}

					.item-image {
						display: flex;
						position: relative;

						&:before {
							content: '';
							width: 100%;
							height: 100%;
							position: absolute;
							top: 0;
							left: 0;
							background: rgba(255, 255, 255, 0.5);
							border-radius: 10px 10px 0 0;
							opacity: 0;
							transition: all 0.3s ease-in;
						}
					}

					.item-content {
						background: #FFFFFF;
						box-shadow: 0px 10px 40px rgba(39, 52, 125, 0.12);
						border-radius: 0 0 10px 10px;
						padding: 20px 20px 24px;

						h3.title {
							margin-bottom: 12px;

							a {
								font-family: Roboto;
								font-style: normal;
								font-weight: bold;
								font-size: 16px;
								line-height: 24px;
								color: #EE0033;
							}
						}

						.des {
							font-family: Roboto;
							font-style: normal;
							font-weight: 500;
							font-size: 20px;
							line-height: 24px;
							color: #0C0033;
							margin-bottom: 20px
						}

						.date {
							font-family: Roboto;
							font-style: normal;
							font-weight: normal;
							font-size: 14px;
							line-height: 16px;
							color: #576C8A;
						}
					}
				}
			}
		}
	}

	.navpage {
		margin: 35px 0 100px;

		ul {
			li {
				span {
					color: #E7E7E7;
				}
			}
		}
	}
}

