					position: relative;
					padding-right: 36px;
					height: 50px;
					width: 50%;
					margin-right: 11px;

					.img {
						display: inline-block;

						img {
							height: 37px;
							max-height: none;
							margin: 6px 5px;
						}
					}

					.refesh {
						position: absolute;
						right: 9px;
						top: 12px;
						width: 25px;
						height: 25px;
						display: block;
						cursor: pointer;
					}
				}

				input {
					font-size: 14px;
					color: #BFC4CE;
					padding: 14px 16px;
				}
			}
		}

		.action {
			a {
				width: 100%;
				padding: 15px 30px;
				margin-bottom: 10px;
			}
		}

		.modal-close {
			top: 12px;
		}

		.close-modal {
			display: none;
		}
	}

	&.cancel-service__popup {
		max-width: 600px;

		h1.title-popup {
			text-align: center;
		}

		.action {
			display: flex;
			justify-content: center;

			a.btn {
				font-family: Montserrat;
				font-style: normal;
				font-weight: normal;
				font-size: 14px;
				line-height: 20px;
				text-align: center;
				color: #EE0033;
				background: transparent;
				width: 36%;

				&.action-back {
					margin-right: 40px;
				}

				&:hover {
					background: #14afb4;
					border-color: #14afb4;
					color: #fff;
				}
			}
		}

		.close-modal {
			display: none;
		}
	}
