// *********************
// UPLOAD HO SO
// ********************

.btn-upload {
    width: 180px;
    height: 180px;
    background: #e7e7e7;
    border-radius: 10px;
    position: relative;
    margin: 0 auto;

    label {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;

        i {
            font-size: 32px;
            color: @color-1;
        }
    }
}

.file-upload {
    display: none;
}

.frm-upload {
    form {
        width: 89%;
        margin: 0 auto 64px;
        text-align: center;
    }

    .txt-blue-light {
        margin-bottom: 60px;
    }

    .txt-black {
        margin-bottom: 40px;
    }

    .upload-note {
        color: #14d6cf;
        margin-bottom: 60px;
        display: block;
    }

    .agree {
        font-style: normal;
    }

    .checkbox {
        display: inline-block;
        margin-bottom: 30px;
    }
}

.form-group--upload {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    &::after {
        display: none;
    }

    .label {
        color: #576c8a;
        font-size: 16px;
        display: block;
        margin-bottom: 10px;
        text-align: center;
    }

    @media (max-width: 767px) {
        .form-group-item {
            width: 100%;
            text-align: center;

            &+.form-group-item {
                margin-top: 30px;
            }
        }
    }
}

