.intro-statistics {
    background: #f7f7f7;
    padding: 64px 0;

    &__wrap {
        display: flex;
        flex-wrap: wrap;
    }

    &__item {
        width: calc(50% - 15px);
        padding: 45px 25px;
        text-align: center;

        .big-title a {
            line-height: 1.5;

            &:hover {
                color: @color-blue-light;
            }
        }

        .icon {
            display: inline-block;
            margin-bottom: 30px;

            &+* {
                margin-bottom: 8px;
            }
        }

        >a {
            color: @color-black;
            text-decoration: underline;
            line-height: calc(54 / 16);
            font-weight: 700;

            &:hover {
                color: @color-1;
            }
        }

        &:nth-child(-n + 2) {
            margin-bottom: 30px;
        }

        &:nth-child(even) {
            margin-left: 30px;
        }
    }

    @media (max-width: 767px) {
        &__item {
            width: 100%;

            &:nth-child(even) {
                margin-left: 0;
            }

            &:nth-child(3) {
                margin-bottom: 30px;
            }
        }
    }
}

.intro-brand {
    margin-top: 60px;

    &__wrap {
        .box-shadow;
        padding: 30px 20px;
