            .owl-stage-outer {
                padding-top: 30px;
            }

            h3 {
                font-size: 16px;
            }
        }
    }
}

.service-steps {
    background: #f7f7f7;
    padding: 30px 0;
    .vw(margin-bottom, 64px);

    &__wrap {
        display: flex;
        flex-wrap: wrap;
    }

    &__item {
        width: calc((100% - 90px) / 4);
        background: #ffffff;
        box-shadow: 0px 10px 40px rgba(39, 52, 125, 0.12);
        border-radius: 10px;
        padding: 30px 15px;
        text-align: center;

        .icon {
            display: block;
            margin-bottom: 16px;
        }

        >p {
            font-weight: 500;
            font-size: 18px;
            color: @color-1;
            line-height: 1.33;
        }

        &:hover {
            box-shadow: 30px 30px 60px rgba(0, 0, 0, 0.4);
            border-radius: 12px;
        }

        @media (min-width: 992px) {
            &+div:not(:nth-child(4n + 1)) {
                margin-left: 30px;
            }

            &:nth-child(n + 5) {
                margin-top: 20px;
            }
        }
    }

    @media (max-width: 991px) {
        &__item {
            width: calc(50% - 15px);

            &+div:not(:nth-child(2n + 1)) {
                margin-left: 30px;
            }

            &:nth-child(n + 3) {
                margin-top: 20px;
            }
        }
    }
}

.service-steps-details {
    .content {
        >h3 {
            font-size: 24px;
            color: @color-black;
            margin-bottom: 20px;
            line-height: 1.33;

            &~p {
                .vw(padding-left, 30px);
            }

            &+h3 {
                margin-top: 64px;
            }
        }

        >h4 {
            .vw(padding-left, 30px);
            font-size: 20px;
            font-weight: 500;
            color: @color-black;
            margin-bottom: 20px;
            line-height: 1.33;

            &~p {
                .vw(padding-left, 60px);
            }
        }

        >p {
            margin-bottom: 10px;
            color: @color-grey;
            line-height: 1.8;

            &.dv-cungcap {
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;

                label {
                    font-size: 20px;
                    font-weight: 500;
                }

                a {
                    font-size: 20px;
                    letter-spacing: 0.125px;
                    color: @color-1;
                    text-decoration: underline;
                }
            }

            &+h3 {
                .vw(margin-top, 64px);
            }

            &+h4 {
                .vw(margin-top, 30px);
            }

            &+p {
                padding-top: 10px;
                border-top: 1px solid rgba(197, 200, 219, 0.3);
            }
        }
    }
}

.service-report {
    .box-shadow;
    .vw(margin-bottom, 64px);

    &__head {
        background: rgba(20, 175, 180, 0.15);
        border-radius: 10px 10px 0px 0px;
        height: 71px;
        line-height: 71px;
        text-align: center;
        font-size: 24px;

