.report-tabs {
    &__nav {
        display: flex;
        margin-bottom: 40px;

        li {
            width: 25%;
            text-align: center;
            font-weight: 300;
            font-size: 16px;
            color: @color-1;
            padding: 15px;
            border: 2px solid #eef1f7;

            &:first-child {
                border-radius: 10px 0px 0px 10px;
            }

            &:last-child {
                border-radius: 0px 10px 10px 0px;
            }

            &+li {
                border-left: 0;
            }

            &:hover,
            &.current {
                background: @color-1;
                border-color: @color-1;
                color: #ffffff;
            }
        }
    }

    &__content {
        color: @color-black;

        p {
            &+p {
                margin-top: 14px;
            }
        }
    }
}

.dv-truyenhinh {
    display: flex;
    margin-bottom: 64px;

    li {
        width: calc(100% / 3 - 20px);
