.account-addional {
    min-height: 380px;
    max-height: 100%;

    h2.title-h2 {
        font-family: @font-title;
    }

    .section-desc {
        font-size: 18px;
        color: @color-black;
    }

    &__wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    &__item {
        width: calc((100% - 60px)/3);
        padding: 15px;
        box-shadow: 4px 4px 16px rgba(0, 0, 0, 0.08);
        border-radius: 10px;

        &+div {
            margin-left: 30px;
        }

        .radio-custom {
            padding-left: 0;

            .checkmark {
                top: 50%;
                transform: translate(0, -50%);
            }
        }
    }

    .owl-carousel {
        .account-addional__item {
            width: 100%;
        }

        .owl-nav {
            button.owl-prev {
                transform: translate(-105%, -50%);
            }

            button.owl-next {
                transform: translate(105%, -50%);
            }
        }
    }

    &__verification {
        width: 750px;
        max-width: 100%;
        margin: 60px auto;

        h3 {
            font-size: 24px;
            line-height: calc(28/24);
            margin-bottom: 30px;
        }

        .form-group {
            display: flex;
            flex-wrap: wrap;

            .btn {
                width: calc((100% - 30px)/2);
                font-weight: 300;
                font-family: @font-title;

                &+.btn {
                    margin-left: 30px;
                }
            }
        }

        >p {
            margin-top: 15px;
            font-size: 16px;
            line-height: 1.8;
            color: #474747;
        }
    }

    @media (max-width: 767px) {
        &__wrap {
            margin-bottom: 30px;
        }

        &__item {
            width: 100%;

            &+div {
                margin-left: 0;
                margin-top: 20px;
            }
        }

        &__verification {
            .form-group .btn {
                width: 100%;

                &+.btn {
                    margin-left: 0;
                    margin-top: 20px;
                }
            }
        }
    }
}

