.c-bar-process {
    padding: 0 0 45px;

    .inner {
        overflow: hidden;
    }

    &__content {
        // margin: 10px calc((24px*4 - 100%)/6 + 4px);
        max-width: 1100px;

        li {
            width: 25%;
            float: left;
            position: relative;

            &:first-child {

                &:after {
                    width: 50%;
                    left: 50%;
                }
            }

            &:last-child {

                &:after {
                    width: 50%;
                    left: 0;
                }
            }

            &.active,
            &.activated {

                .c-bar-process__check {
                    background-color: #EE0033;
                    color: #fff;

                    &:before {
                        content: "\e920";
                        font-family: 'icon-theme' !important;
                        speak: none;
                        font-style: normal;
                        font-weight: normal;
                        font-variant: normal;
                        text-transform: none;
                        line-height: 12px;
                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                        font-size: 13.5px;
                    }
                }

                .c-bar-process__text__bold {
                    color: #EE0033;
                }
            }

            &.activated {

                &:after {
                    width: 100%;
                    border-color: #d65470;
                }
            }

            &:after {
                background-color: #B70D02;
                content: "";
                height: 4px;
                left: 0;
                position: absolute;
                right: 0;
                top: 13px;
                transform: translateY(-50%);
                width: 100%;
            }

            .c-bar-process__check {
                background-color: #fff;
                border-radius: 50%;
                display: block;
                height: 26px;
                line-height: 26px;
                position: relative;
                text-align: center;
                width: 26px;
                z-index: 1;
                margin: 0 auto;
                border: 2px solid #EE0033;
            }
        }
    }

    &__text {
        text-align: center;
