//===================
// NOTIFICATION 
//===================

.notification {
    div:after {
        display: none;
    }

    h2.title-h2 {
        margin-bottom: 65px;
    }

    .tit {
        margin-bottom: 65px;
    }

    &__filter {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 40px;

        select {
            width: 165px;
        }
    }

    &__item {
        display: flex;
        justify-content: space-between;
        box-shadow: 0px 10px 40px rgba(39, 52, 125, 0.12);
        border-radius: 10px;
        padding: 30px;
        margin-bottom: 30px;

        &+& {}
    }

    &__img {
        width: 135px;
    }

    &__info {
        width: calc(100% - 135px - 125px - 30px);

        >p {
            font-size: 16px;
            line-height: 180%;
            color: @color-black
        }

    }

    &__title {
        display: flex;
        align-items: center;
        margin-bottom: 20px;

        h3 {
            font-size: 24px;
            line-height: 28px;
            color: @color-black;
            margin-right: 15px;
        }

        span {
            color: @color-grey;
            font-weight: 500;
        }
    }

    &__action {
        width: 95px;
        align-items: center;
        display: flex;
        justify-content: space-between;

        >a {
            font-size: 28px;
            color: @color-grey;

            &:hover,
            &.active {
                color: @color-red;
            }

            &.fs-heart {

                &.active,
                &:hover {
                    &::before {
                        background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='28' height='27' viewBox='0 0 28 27' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M19.8329 0.00419878C18.745 0.0102752 17.6691 0.233333 16.6678 0.660414C15.6664 1.0875 14.7596 1.71009 14 2.49201C12.8723 1.32164 11.4223 0.515899 9.8357 0.178005C8.24908 -0.15989 6.59811 -0.0145504 5.09428 0.595404C3.59045 1.20536 2.30223 2.25216 1.39465 3.6017C0.487068 4.95124 0.00144869 6.54208 0 8.17042C0 15.6377 12.7196 26.1307 13.2618 26.5703C13.4705 26.7405 13.7312 26.8333 14 26.8333C14.2688 26.8333 14.5295 26.7405 14.7382 26.5703C15.2804 26.1307 28 15.6377 28 8.17042C27.9929 6.00103 27.1291 3.92302 25.5981 2.39214C24.0671 0.861258 21.9937 0.00249547 19.8329 0.00419878Z' fill='%23FF0048'/%3e%3c/svg%3e ");
                        background-repeat: no-repeat;
                        background-size: contain;
                    }
                }
            }
        }
    }

    .tab-title {
        li {
            a {
                display: flex;
                align-items: center;

                &:before {
                    content: "";
                    width: 15px;
                    height: 15px;
                    border-radius: 50%;
                    display: inline-block;
                    background: @color-red;
                    margin-right: 8px;
                }
            }

            &.active {
                a::before {
                    display: none;
                }
            }
        }
    }

    // Quickview 

    &-qv {
        background: #FFFFFF;
        z-index: 99;
        box-shadow: 0px 10px 40px rgba(39, 52, 125, 0.12);
        border-radius: 10px;
        color: @color-black;
        position: absolute;
        width: 612px;
        right: -40px;
        display: none;

        .open & {
            display: block;
        }

        margin-top: 21px;

        &::before {
            content: "";
            width: 0;
            height: 0;
            right: 40px;
            position: absolute;
            border-style: solid;
            border-width: 0 15px 15px 15px;
            border-color: transparent transparent #ffffff transparent;
            transform: translate(0, -100%);
        }

        @media (max-width: 1199px) {
            right: 0px;

            &::before {
                right: 0;
            }
        }

        @media (max-width: 991px) {
            max-width: 100%;

            &::before {
                right: 160px;
            }
        }

        &__list {
            max-height: calc(536px - 105px);
            padding: 25px 25px 0;
            position: relative;
        }

        &__item {

            display: flex;
            .flex-spacebetween;

            &:last-child {
                padding-bottom: 0;
            }

            &+& {
                padding-top: 25px;
                margin-top: 25px;
                border-top: 1px solid #EAEAEA;
            }

            &--notseen {
                .notification-qv__title h3 {
                    font-weight: 700;
                }

                .notification-qv__info p {
                    font-weight: 500;
                }

                .notification-qv__img {
                    position: relative;

                    &::after {
                        content: "";
                        width: 15px;
                        height: 15px;
                        background: @color-red;
                        position: absolute;
                        right: 0;
                        top: 0;
                        border-radius: 50%;
                        transform: translate(50%, -50%);

                    }
                }
            }
        }

        &__footer {
            padding: 25px;

            a {
                margin-left: calc(100px + 23px);
            }
        }

        &__img {
            width: 100px;
        }

        &__info {
            width: calc(100% - 100px - 20px);

            p {
                font-size: 16px;
                line-height: 180%;
                color: @color-black;
            }

            span {
                color: #576C8A;
                font-size: 14px;
            }
        }

        &__title {
            h3 {
                font-size: 24px;
                line-height: calc(28/24);
                font-weight: 500;
            }
        }
    }

    @media (max-width: 767px) {
        &__item {
            flex-wrap: wrap;
            justify-content: center;
            padding: 20px;
        }

        &__title {
            flex-wrap: wrap;

            h3 {
                margin-right: 0;
                margin-bottom: 5px;
            }
        }

        &__info {
            width: 100%;
            margin-top: 20px;
            margin-bottom: 20px;
        }
    }
}

