.page-ttsk .content.tabs .tabcontent .content-tab .item .item-content {
	h3.title a {
	    display: -webkit-box;
	    -webkit-line-clamp: 1;
	    -webkit-box-orient: vertical;
	    overflow: hidden;
	    text-overflow: ellipsis;
	}
	.des {
		display: -webkit-box;
	    -webkit-line-clamp: 3;
	    -webkit-box-orient: vertical;
	    overflow: hidden;
	    text-overflow: ellipsis;
	}
}

.page-ttsk .content.tabs .tabcontent .content-tab .item {
    background: #FFFFFF; 
    box-shadow: 0px 10px 40px rgba(39, 52, 125, 0.12); 
    border-radius: 10px;
   	overflow: hidden;
    .item-content {
    	box-shadow: none; 
	    border-radius: 0;
    }
	.item-image {
		position: relative;
		height: 0; 
		overflow: hidden;
		padding-bottom: 60%;
		img {
			position: absolute;
			top: 0;
			object-fit: cover;
			width: 100%; height: 100%;
			left: 0;
		}
	}
	&.first-item {
		width: 750px;
		max-width: 100% !important;
		overflow: hidden;
	}
}

.page-qltkdd .pack-data__date .pack-data__circle,
.page-qltkdd .pack-data__data .pack-data__circle {
	background: none;
	padding: 0;
	height: auto;
	position: relative;
	.pack-info {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		margin: auto;
	}
}

.page-ttsk .content.tabs .tabcontent .content-tab .item.first-item .item-content {
	z-index: 2;
}

.page-ttsk .content.tabs .tabcontent .content-tab .item:hover .item-image:before {
	opacity: 1;
	z-index: 1;
}
.main-video-ct .video-tt .videott-content .videott-content_item .item-image img,
.main-htkh .support-online .support-online_content .slick-slider .slick-list .slick-track .slick-slide .item .item-image img {
	width: 100%;
}
div.main.main-chatonline {
	background-color: #fff;
}
@media (max-width: 1199px) { 
	.page-ttsk .content.tabs .tabcontent .content-tab .item.first-item {
	    width: 653px;
		max-width: 100% !important;
	}
}