  border-radius: 10px;
  width: 100%;
  line-height: 20px;
  padding: 15px 16px;
  background: #fff url('../images/select.svg') no-repeat;
  background-position-x: calc(100% - 16px);
  background-position-y: 46%;
  font-weight: 500;
  color: #576C8A;
  font-family: "Roboto", sans-serif;
}
input,
textarea {
  border: 1px solid #E7E7E7;
