  padding: 5px 0 0 0;
}
.radio-custom1 .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
.radio-custom1 input:checked ~ .checkmark ~ span {
  font-weight: 500;
}
.radio-custom1 input:checked ~ .checkmark:after {
  display: block;
  background: #EE0033;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  position: absolute;
  top: 3px;
  left: 3px;
