@import "../css/fonts/iconfont/iconfont.css";
@color-1: #14afb4;
@color-blue-light: #14d6cf;
@color-grey: #576c8a;
@color-black: #0c0033;
@font-title: "Montserrat";
@color-red :#FF0000;
@color-gold :#FE9A00;

// Mixin
.vw-fontsize (@vw-px-size, @vw-breakpoint) {
    @vw-context: (@vw-breakpoint * 0.01);
    @vw-get: (@vw-px-size / @vw-context);
    font-size: (@vw-px-size * 1px); //Fallback in px
    font-size: (@vw-get * 1vw);
}

.vw(@prop, @value) {
    @{prop}: @value;
    @{prop}: 1vw * @value / (1440 * 0.01);
}

// ================================

// j-Tabs
.j-tabs {
    .tab-link {
        cursor: pointer;
    }

    .tab-content {
        display: none;
    }

    .tab-content.current {
        display: inherit;
    }
}

// ================================

.d-flex {
    &__wrap {
        display: flex;
        flex-wrap: wrap;
        flex-basis: 0;
    }

    &__item {
        flex-grow: 1;
        flex-shrink: 1;
        flex-basis: 1;
        .box-shadow;
    }
}

[class*="__flex"] {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    &--column {
        flex-direction: column;

    }

    &::after {
        display: none;
    }
}

.flex-column {
    flex-direction: column;
}

.flex-spacebetween {
    justify-content: space-between;

    &::after {
        display: none;
    }
}

input:read-only {
    color: #E7E7E7;
    cursor: default;
}



.radio-custom {
    .checkmark {
        &--right {
            right: 0;
            left: auto;
        }

        &--top {
            top: 0 !important;
            transform: translate(0) !important;
        }
    }
}

.btn {
    font-weight: 300;
}

.thumb-img {
    position: relative;
    height: 0;
    overflow: hidden;

    img {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.thumb-1x1 {
    padding-bottom: 100%;
}

.thumb-5x3 {
    padding-bottom: 60%;
}

.thumb-16x9 {
    padding-bottom: 56.25%;
}

a {
    color: inherit;
}

.center {
    margin: 0 auto;
}

.txt-title {
    font-family: @font-title;
}

.sub-title {
    display: block;
    font-weight: bold;
    font-size: 24px;
    line-height: calc(28/24);
}

.clearfix {

    &::before,
    &::after {
        content: "";
        display: table;
        clear: both;
    }
}

.box-shadow {
    box-shadow: 0px 10px 40px rgba(39, 52, 125, 0.12);
}

.txt-black {
    color: #0c0033;
}

.txt-blue-light {
    color: #14d6cf;
}

.txt-blue {
    color: #14afb4;
}

.txt-grey {
    color: @color-grey;
}

.txt-red {
    color: @color-red;
}

.txt-gold {
    color: @color-gold;
}

.txt-medium {
    font-weight: 500;
}

.txt-bold {
    font-weight: 700;
}

.txt-20 {
    font-size: 20px;
}

.txt-24 {
    font-size: 24px;
}

.txt-18 {
    font-size: 18px;
}

.txt-16 {
    font-size: 16px;
}

.txt-12 {
    font-size: 12px;
}

.txt-36 {
    font-size: 36px;
}

.txt-underline {
    text-decoration: underline;
}

.txt-uppercase {
    text-transform: uppercase;
}

.big-title {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 36px;
}

.form-note {
    display: block;
    font-style: italic;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    color: #576c8a;
    font-family: "Roboto";
    text-transform: none;
}

.w-100 {
    width: 100%;
}

.lg\:w-100 {
    @media (min-width: 1200px) {
        width: 100% !important;
    }
}

.md\:w-100 {
    @media (max-width: 1199px) and (min-width: 992px) {
        width: 100% !important;
    }
}

.sm\:w-100 {
    @media (max-width: 991px) and (min-width: 768px) {
        width: 100% !important;
    }
}

.xs\:w-100 {
    @media (max-width: 767px) {
        width: 100% !important;
    }
}

