.btn {
    &.txt-red {
        border-color: @color-red;
        border-width: 1px;

        &:hover {
            background-color: @color-red;
        }
    }
}

.btabs.txt-center {
    .tab-title {
        li {
            float: none;
            display: inline-block;

            &+li {
                margin-left: 70px;
            }

            a {
                padding-left: 0;
                padding-right: 0;
            }
        }
    }
}

.form-group-item .btn-upload .preview {
    position: relative;
    z-index: 1;
}