// mixin hide text in element

.hide__text {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

// fix dots slide show

.banner-slideshow {

    button {

        &.owl-dot {

            span {
                width: 20px;
                height: 20px;
            }

            &:hover,
            &.active {

                span {
                    background: transparent url("../images/i-dot.svg") no-repeat;
                    background-position: -17px 0;
                    background-size: cover;
                    top: -1px;
                    position: relative;
                }
            }
        }
    }
}

