        }

        &__content {
            display: flex;
            flex-flow: row wrap;

            &__left,
            &__right {
                width: 30%;
            }

            &__mid {
                width: 40%;
            }

            &__left {
                padding: 5px 0 0;

                &__text {
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 19px;
                    text-align: center;
                    color: #FFFFFF;
                    position: relative;
                    top: -2px;
                    margin-right: 10px;
                }

                &__price {
                    font-family: 'Montserrat';
                    font-weight: 500;
                    font-size: 24px;
                    line-height: 50px;
                    text-transform: uppercase;
                    color: #FFFFFF;

                    &.before--money {

                        &:after {
                            content: '';
                            background: transparent url("../images/i-check.svg") no-repeat left top;
                            background-size: contain;
                            width: 18px;
                            height: 12px;
                            display: inline-block;
                            margin-left: 10px;
                        }
                    }
                }
            }

            &__mid {
                text-align: center;

                &__text {
                    font-family: 'SF Pro Text';
                    background: #FE9A00;
                    border-radius: 2px;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 30px;
                    text-align: center;
                    color: #FFFFFF;
                    display: inline-block;
