                            padding-bottom: 15px;
                        }

                        p {
                            padding-bottom: 15px;
                        }
                    }

                    &__price {
                        text-align: left;
                        width: 100%;
                    }
                }

                &__total {
                    padding: 20px 20px 27px;
                }
            }
        }
    }
